<template>
    <v-row>
        <v-col>
            <v-card class="mb-4">
                <v-card-title>
                </v-card-title>
                <v-card-text class="d-flex">
                    <v-btn size="small" class="button-spacing" icon="mdi-plus" @click="showFilterDialog = !showFilterDialog"></v-btn><v-label>Add Event</v-label>
                </v-card-text>
            </v-card>
             <data-table :tableData="tableData" @pageChanged="pageChanged">
                <template v-slot:[`item.dutyDate`]="{ item }">
                    <span>{{ moment(item.raw.dutyDate).format("D-MMM-yyyy") }}</span>
                </template>
                <template v-slot:[`item.reportingTime`]="{ item }">
                    <span>{{ moment(item.raw.reportingTime).format("hh:mm A") }}</span>
                </template>
                <template v-slot:[`item.Actions`]="{ item }">
                  <div class="pa-2">
                    <v-btn icon="mdi-eye-circle-outline" class="button-spacing" size="small"  @click="markAttendance(item.raw.attendanceId)"/>
                    <v-btn icon="mdi-square-edit-outline" class="button-spacing" size="small" @click="updateEvent(item.raw.attendanceId)"  />
                    <v-btn icon="mdi-trash-can-outline" class="button-spacing" size="small" @click="deleteAttendance(item.raw.attendanceId)" />
                  </div>
                </template>
            </data-table>
        </v-col>
    </v-row>
    <v-dialog v-model="showFilterDialog" width="500" title="Attendance">
            <v-card>
            <v-card-title>
                Create Event
            </v-card-title>
            <v-card-text>
         <v-row>
        <v-col>
            <v-card>
                <v-card-title>
                   Add Event
                </v-card-title>
                <v-card-text>
                 <v-form @submit.prevent="submitForm">
                   <v-autocomplete label="Attendance Type" v-model="attendanceMaster.attendanceTypeId" :items="attendanceType" item-title="description"
                            item-value="generalID" required/>
                    <v-text-field v-model="attendanceMaster.description" :rules="textRules" label="Description" required/>
                    <v-label class="custom-margin-top custom-margin-bottom">Duty Date & Time</v-label>
                    <VueDatePicker v-model="attendanceMaster.dutyDate"></VueDatePicker>
                    <v-label class="custom-margin-top custom-margin-bottom">Reporting Date & Time</v-label>
                    <VueDatePicker v-model="attendanceMaster.reportingTime"></VueDatePicker>
                    <v-label class="custom-margin-top custom-margin-bottom">Scanning Start Time</v-label>
                    <VueDatePicker v-model="attendanceMaster.scanningStart" type="date"></VueDatePicker>
                    <v-label class="custom-margin-top custom-margin-bottom">Scanning End Time Date</v-label>
                    <VueDatePicker v-model="attendanceMaster.scanningEnd" type="date"></VueDatePicker>
                    <v-btn v-if="!isUpdateEvent" size="small" class="custom-margin-top" type=submit>Add Event</v-btn>
                    <v-btn v-if="isUpdateEvent" size="small" class="custom-margin-top" type=submit>Update</v-btn>
                </v-form>
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
    <v-row>
        <v-col>
            <v-btn @click="close" color="grey" class="mr-2">Close</v-btn>
        </v-col>
    </v-row>
                </v-card-text>
            </v-card>
    </v-dialog>
</template>
<script setup>
import { ref, reactive, onMounted, inject } from 'vue';
import { getAll, getById, getAttendance, updateMasterAttendance,  deleteMasterAttendance } from '@/services/attendanceService';
import { getAttendanceType } from '@/services/utilitiesService';
import { addMasterAttendance } from '@/services/attendanceService';
import { useRouter } from "vue-router";
import moment from 'moment';

const loader = inject("loader");
const snackbar = inject("snackBar");
const router = useRouter();

const attendanceType = ref([]);
const showFilterDialog = ref(false);
const isUpdateEvent = ref(false);
const attendanceId = 3660;
const event = ref({});
const attendance = ref([]);
const pageContext = reactive({
    pageNumber: 1,
    pageSize: 20,
    totalItems: 0,
    totalPages: 0,
    hasNextPage: false,
    hasPreviousPage: false,
    pageIndex: 0,
    search: "",
    organizationId: 0,
    showDivisionEvents: true
});

let attendanceMaster = reactive({
    attendanceTypeId: null,
    organizationId: 0,
    description: null,
    dutyDate: null,
    reportingTime: null,
    scanningStart: null,
    scanningEnd: null
});

const tableData = reactive({
    headers: [
        { title: "S. No", key: "SNo" },
        { title: "Duty Date", key: "dutyDate" },
        { title: "Details", key: "description" },
        { title: "Reporting Time", key: "reportingTime" },
        { title: "Actions", key: "Actions" }
    ],
    data: [],
    itemsPerPage: 15,
    totalItems: 0,
    paginationContext: {},
});

async function getData() {
    loader.show();

    let result = await getAll(pageContext)
        .catch(error => console.log(error))
        .finally(() => {
            loader.hide();
        });

    console.log(result);

    tableData.data = result.events;
    tableData.paginationContext = result.listContext;
}

async function getAttendanceData(attendanceId) {
    loader.show();

    let result = await getById(attendanceId)
        .catch(error => console.log(error))
        .finally(async () => {
            await getDetail();
            loader.hide();
        });
    event.value = result;
}

async function getDetail() {
    let result = await getAttendance(attendanceId)
        .catch(error => console.log(error))

    attendance.value = result;
}

function markAttendance(attendanceId) {
   router.push(`/attendance/${attendanceId}`);
}

async function deleteAttendance(attendanceId) {
       await deleteMasterAttendance(attendanceId)
        .then(() => snackbar.success("Attendance has been marked successfully"))
        .catch(error => {
            snackbar.error(error);
            console.log(error);
        })
        .finally(async () => {
            await getData();
            loader.hide();
        });
}

async function pageChanged(pageNo) {
    pageContext.pageNumber = pageNo;
    await getData();
}

async function close() {
    attendanceMaster.attendanceTypeId = null;
    attendanceMaster.organizationId = null;
    attendanceMaster.description = null;
    attendanceMaster.dutyDate = null;
    attendanceMaster.reportingTime = null;
    attendanceMaster.scanningStart = null; 
    attendanceMaster.scanningEnd = null;
    if (isUpdateEvent.value)
        isUpdateEvent.value = !isUpdateEvent.value;
    showFilterDialog.value = !showFilterDialog.value;
}

async function submitForm() {
    loader.show();
    let result = null;
    if (isUpdateEvent.value)
    {
        result = await updateMasterAttendance(attendanceId, attendanceMaster)
        .then(() => { 
            snackbar.success("Event has been updated!");
            showFilterDialog.value = !showFilterDialog.value;
        })
        .finally(() => {
        loader.hide();
        getData();
        });
    }
    else {
        result = await addMasterAttendance(attendanceMaster)
        .then(() => { 
            snackbar.success("Event has been created!");
            showFilterDialog.value = !showFilterDialog.value;
        })
        .finally(() => {
        loader.hide();
        getData();
        });
    }
    
   console.log(result);
}

async function updateEvent(attendanceId) {
    await getAttendanceData(attendanceId);
    attendanceMaster.attendanceTypeId = event.value.attendanceTypeId;
    attendanceMaster.organizationId = event.value.organizationId;
    attendanceMaster.description = event.value.description;
    attendanceMaster.dutyDate = event.value.dutyDate;
    attendanceMaster.reportingTime = event.value.reportingTime;
    attendanceMaster.scanningStart= event.value.scanningStart;
    attendanceMaster.scanningEnd = event.value.scanningEnd;
    isUpdateEvent.value = !isUpdateEvent.value;
    showFilterDialog.value = !showFilterDialog.value;
}

onMounted(async () => {
    loader.show();
    attendanceType.value = await getAttendanceType()
    .catch(error => console.log(error));
    await getData();
});


</script>

<style>
.button-spacing {
    margin: 10px;
}

.v-card {
    overflow: unset !important;
}

.custom-margin-top {
    margin-top: 10px;
}

.custom-margin-bottom {
    margin-bottom: 10px;
}

</style>