<template>
    <v-row>
        <v-col>
            <v-card>
                <v-card-title>
                   Add Master Attendance
                </v-card-title>
                <v-card-text>
                 <v-form @submit.prevent="submitForm">
                   <v-autocomplete label="Attendance Type" v-model="attendanceMaster.attendanceTypeId" :items="attendanceType" item-title="description"
                            item-value="generalID" required/>
                    <v-text-field v-model="attendanceMaster.description" :rules="textRules" label="Description" required/>
                    <v-text-field v-model="attendanceMaster.dutyDate" type="date" label="Duty Date"></v-text-field>
                    <v-label class="custom-margin-top custom-margin-bottom">Reporting Date & Time</v-label>
                    <VueDatePicker v-model="attendanceMaster.reportingTime"></VueDatePicker>
                    <v-label class="custom-margin-top custom-margin-bottom">Scanning Start Time</v-label>
                    <VueDatePicker v-model="attendanceMaster.scanningStart" type="date"></VueDatePicker>
                    <v-label class="custom-margin-top custom-margin-bottom">Scanning End Time Date</v-label>
                    <VueDatePicker v-model="attendanceMaster.scanningEnd" type="date"></VueDatePicker>
                    <v-btn size="small" class="custom-margin-top" type=submit>Add Master Attendance</v-btn>
                </v-form>
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
    
</template>
<script setup>

import { ref, reactive, inject, onMounted  } from 'vue';
import { getAttendanceType } from '@/services/utilitiesService';
import { addMasterAttendance } from '@/services/attendanceService';

const loader = inject("loader");
const snackbar = inject("snackBar");
const attendanceType = ref([]);
// const events = ref([]);
const attendanceMaster = reactive({
    attendanceTypeId: null,
    organizationId: 0,
    description: null,
    dutyDate: null,
    reportingTime: null,
    scanningStart: null,
    scanningEnd: null
});


async function submitForm() {
    console.log(attendanceMaster)
    loader.show();
    let result = await addMasterAttendance(attendanceMaster)
        .then(() => snackbar.success("Attendace has been created!"))
        .finally(() => {
        loader.hide();
        });
   console.log(result);

}

onMounted(async () => {
    attendanceType.value = await getAttendanceType()
        .catch(error => console.log(error));
    // events.value = await getAllEvents()
    //     .catch(error => console.log(error));
});
</script>


<style>
.v-card {
    overflow: unset !important;
}

.custom-margin-top {
    margin-top: 10px;
}
.custom-margin-bottom {
    margin-bottom: 10px;
}
</style>